body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: url(/static/media/pins-map.cb0de938.jpg);
  background-color: #cccccc;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navLogo {
    margin-right: 10px;
}
.main {
    padding-top: 50px;
}

.selectscreen .card {
    width: 335px;
    margin-bottom: 10px;
    opacity: .8;
    text-align: center;


}

.returnButton{
    text-align: center;
    
    padding-top: 20px;



}

#gameExitBtn {
    margin: 0px;
    
    
}

#endscreen {
    padding: 20px;
    text-align: center;
    color: white;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.3);
        padding-bottom : 150px;


}

.endscreen {

    text-align: center;

    
    margin-top: 70px;
    font-size: 40px;

}
#endscreenForm input[type=text]:focus {
    border: 3px solid white;
  }

#endscreenForm input{
    margin-top: 20px;
    
    padding: 15px 30px 15px 30px;
    border: none;
}

.card {
    width: 250px;
   margin-bottom: 10px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    opacity: .9;
    text-align: center;
    border: 0;

    margin-right:0;
}
.card:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.5);
    -webkit-transform: scale(1.03);
            transform: scale(1.03);
    opacity: 1;
}
.card .card-img-top {
    height: 220px;
}

.selectscreen .card-img-top {
    height: auto;
}

.card-text {
    padding-bottom: 10px;
}

.title {
    text-align: center;
    font-size: 250%;
    margin-bottom: 50px;
    padding-top: 15px;
    color: #f8fae5;
}

.title2 {
    text-align: center;
    font-size: 180%;
    margin-bottom: 0px;
    padding-top: 0px;
    color: #f8fae5;
}


.cardButton {
    margin-bottom: 60px;
    margin-left: 15%;
}

.cardButton :hover {
    text-decoration: none;
    opacity: 1;
}

#gameTimer {


    color: #ffffff;
    margin-bottom: 20px;
    font-size: 40px;
    text-align: center;
    

}

.subtitle {
    text-align: center;
    padding-top: 20px;
    color: #f8fae5;
    text-decoration: underline;
}

#nameitCard{
    color:black;
    margin-top: 100px;
    float: right;
    margin-right: 50px;
}
#placeitCard{
    color: red;
    margin-top: 100px;
    float: left;
    margin-left: 50px;
}

#submitScore{
    margin-top: 0px;
    padding: 10px 40px 10px 40px;
}
.returnButtonGame {
    text-align: center;
    margin: left;
    padding-top: 20px;
    float: left;


}

.returnButtonGame .btn {
    margin: 10px;
  float: left;
    margin-bottom: 40px ;
}

.questionTitle {
    padding-top: 20px;
   color: #10150e;
    text-align: center;
}

.flag {

    width: 30vw;
    min-width: 580px;
    text-align: center;
    margin: auto;
}
.flag img{
    max-height: 350px;
    margin-top: 60px;
    margin-bottom: 60px;
}

@media only screen and (max-width: 800px) {
    .flag{
        min-width: 100px;
        width: 80vw;

    }
}

.options {
    text-align: center;
    margin: auto;
    padding: 20px;
}
#game1div {
    padding: 40px;
    text-align: center;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.3);
    padding-bottom : 275px;
}

#game1div button{
    margin: 0;
    width: 250px;
    height: 80px;
}
#btn1, #btn3 {
    float: right;

    margin-right: 0px;
}

#btn2, #btn4 {
    float: left;

    margin-left: 0px;

}

#btn1:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.5);
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

#btn2:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.5);
        transition: -webkit-transform .2s;
        transition: transform .2s;
        transition: transform .2s, -webkit-transform .2s;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
#btn3:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.5);
        transition: -webkit-transform .2s;
        transition: transform .2s;
        transition: transform .2s, -webkit-transform .2s;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
#btn4:hover {
    box-shadow: 0 5px 15px rgba(0,0,0,0.5);
        transition: -webkit-transform .2s;
        transition: transform .2s;
        transition: transform .2s, -webkit-transform .2s;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

@media only screen and (max-width: 800px) {
    #game1div button{

        width: 80%;
        min-height: 80px;
        height: 80%;
        float: none;
        margin:0;
    }
}
.progress {

    margin-top: 0px;
    margin-bottom: 0px;
}
.progress-bar {
    width: 10%;
    color: black;
}

.table {

}

.table tr {
    background-color: white;
    opacity: .8;
}

.table tr:hover {
    opacity: 1;
}

.row {
    text-align: center;
    margin: auto;
}

.backButton {
    text-align: center;
    margin: auto;
}
